.VideoListView {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    max-height: 100vh;
    gap:5px;
    margin: 0px 10px;
    background-color: #222;
}

.VideoListView .ListItem {
    display: flex;
    flex-direction: row;
    background-color: #222;
    padding :10px;
    gap: 5px;
    border : 1px solid #FFF;
    border-radius: 10px;
}

.ListItem .ThumbnailField {
    position: relative;
    color: #222;
    width:240px;
    height:135px;
    align-items: center;
}
.ThumbnailField .thumbnail {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 15px;
    object-fit: cover;
    background-color: #FFF;
    z-index: 1;
}
.ThumbnailField p {
    position: relative;
    z-index: 2;
}
.ListItem .profile{
    width:150px;
    display: flex;
    flex-direction: row;
    padding: 5px;
}

.ListItem .text-fields {
    flex: 1;
    margin-top: 5px;
}

.text-fields p {
    margin: 2px;
    padding: 0;
    font-size: 13px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
}
