.Signup {
    display: flex;
    flex-direction: column;
    width: 320px;
    padding: 30px;
    color: #FFF;
    background-color: #222;
    border-radius: 10px;
    gap: 10px;
    border: 1px solid black;
    box-shadow: 1px 1px 1px #000;
    opacity: 1;
    transform: translateX(0%) scale(100%);
    transition: transform 0.8s, opacity 0.8s ease-in-out;
    transform-origin: center;
    z-index: 10;

}

.Signup .InputBox {
    display: flex;
    flex-direction: column;
    padding: 30px;
    color: #FFF;
    background-color: #222;
    border-radius: 10px;
    gap: 10px;
    border: 1px solid black;
    box-shadow: 1px 1px 1px #000;
}

.Signup .InputItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.Signup p {
    text-decoration: none;
    text-shadow: 1px 1px 3px #000;
    margin: 0;
    margin-bottom: 10px;
}

.Signup label {
    margin-right: 5px;
    text-shadow: 1px 1px 3px #000;
}

.Signup input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.Signup .BtnBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    gap: 10px;
}

.Signup button {
    display: flex;
    padding: 8px;
    flex: 1;
    justify-content: center;
    border: 1px solid #999;
    border-radius: 4px;
}

.Signup button:hover {
    background-color: #FFF;
}