section {
    flex-grow: 1;
    flex-direction: column;
    display: flex;
    background-color: #202020;
    padding-left: 10px;
    padding-right: 10px;
    margin-right: 0px;
    overflow: hidden;
}

section .category {
    display: flex;
    height: 50px;
    background-color: #202020;
    padding-left: 30px;
    padding-top: 5px;
    gap: 15px;
}

section .category .selectable {
    cursor: pointer;
    display: flex;
    text-decoration: none;
    font-family: monospace;
    color: #FFF;
    background-color: #111;
    padding: 5px 10px;
    margin-top: 10px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 13px;
    border: 3px solid #111;
}

section .category p {
    display: flex;
    text-decoration: none;
    font-family: monospace;
    color: #333;
    background-color: #fff;
    padding: 5px 10px;
    margin-top: 10px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 13px;
    border: 3px solid #111;
}

section .category .selectable:hover {
    background-color: #333;
    border: 3px solid #333;
    color: #FFF;
    /* color: rgb(100, 233, 100);
    border: 1px solid rgb(100, 233, 100); */
}

section .content {
    flex-grow: 1;
    background-color: #202020;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    margin-bottom: 10px;
    overflow-y: auto;
}

.video-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 15px;
}

.video-item {
    cursor: pointer;
    /* border: 1px solid #ddd; */
    border-radius: 8px;
    transition: transform 0.1s;
    color: #fff
}

.video-item img {
    margin-bottom: 0px;
    width: 100%;
    aspect-ratio: 16/9;
    max-width: 480px;
    border: 1px solid #222;
    border-radius: 8px;
    object-fit: cover;
}

.video-item .profile {
    display: flex;
    align-items: flex-start;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}

.video-item .profile .image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 0px solid black;
    object-fit: cover;
    margin-right: 1rem;
    margin-top: 10px;
}

.text-fields {
    flex: 1;
    margin-top: 5px;
}

.text-fields p {
    margin: 2px;
    padding: 0;
    font-size: 13px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
}

.video-item:hover {
    transform: scale(1.05);
}

.text-fields .bold {
    font-size: 15px;
    font-weight: bold;
}