.watch-container {
    background-color: #000;
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow-y: auto;
}

.video-content {
    flex: 3;
    justify-content: center;
    background-color: #222;
    flex-direction: column;
    /* overflow-y: auto; */
}

.video-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 20px;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    background-color: #222;
}


.video-container video {
    max-width: 1120px;
    max-height: 630px;
    object-fit: contain;
    border-radius: 15px;
    background-color: #000;
}

.video-container .info {
    width: 1120px;
    background-color: #111;
    color: #FFF;
    padding: 5px 15px;
}

.info h2 {
    text-decoration: none;
    margin: 5px;
    padding: 10px;
}

.info .user-info {
    background-color: #222;
    color: #FFF;
    justify-content: space-between;
    flex-direction: row;
    display: flex;
}

.watch-container .user-info p, li, ul {
    text-decoration: none;
    list-style-type: none;
}

.info .user-info .user-info {
    background-color: #222;
    color: #FFF;
    justify-content: baseline;
    flex-direction: row;
    display: flex;
    height: fit-content;
}

.info button {
    margin-top: 10px;
    margin-left: 10px;
    width: 100px;
    height: 50px;
    background-color: #444;
    color: white;
}

.info button:hover {
    background-color: #888;
}

.user-info img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    padding: 10px;
    background-color: #222;
    color: #FFF;
}

.info .more-info {
    display: flex;
    background-color: #666;
    border-radius: 10px;
    padding: 10px;
    flex-direction: column;
}

.comment {
    display: flex;
    flex-grow: 1;
    height: 1200px;
    background-color: #333;
    color: #fff;
}

.video-list {
    flex: 1;
    flex-grow: 1;
    background-color: #111;
    color: #FFF;
    justify-content: center;
    display: flex;
}