.test-container {
    background-color: #fff;
    flex: 1;
    display: flex;
    margin-top: 5px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
}

.test-container input {
    width: 50%;
    height: 30px;
    padding: 0px 10px;
    margin: 0px 0px 0px 10px;
    border-radius: 10px 0px 0px 10px;
    border: 0px solid #FFF;
}

.test-container .video-url {
    margin: 0px;
    padding: 0px 0px;
    width: 80px;
    height: 30px;
    border-top: 0px solid #000;
    border-right: 0px solid #000;
    border-bottom: 0px solid #000;
    border-left: 1px solid #000;
    border-radius: 0px 10px 10px 0px;
}

.test-container .video-url:hover {
    background-color: #33aa22;
}

.test-container .video-content {
    background-color: #222;
    object-fit: contain;
    padding: 10px 40px 40px 40px;
    border-radius: 40px;
    color: #FFF;
}


.test-container .info {
    background-color: #222;
    max-width: 100%;
}

.test-container .user-info {
    max-width: 100%;
}

.test-container .info h2 {
    margin: 5px 5px;
    text-decoration: none;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
}

.test-container .user-info p, li {
    text-decoration: none;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.setup-container {
    position: absolute;
    bottom: 11%;
    right: 25px;
    width: 25%;
    height: 30%;
    border-radius: 15px;
    opacity: 0;
    background-color: rgba(30, 30, 30, 0.6);
    ;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.8);
    animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


