.VideoController {
    position: absolute;
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: 70px;
    bottom: 0;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 0px 0px 15px 15px;
    opacity: 0;
    transform: translateY(100%);
    background: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.0));
    transition: opacity 0s, transform 0.5s;
    z-index: 30;
}

.VideoController .ProgressBar {
    display: flex;
    width: 95%;
    height: 50px;
    align-items: center;
    justify-content: center;
    /* background-color: #000; */
}

.VideoController .seek-bar {
    max-width: 100%;
    max-height: 10px;
    width: 100%;
    height: 5px;
    flex: 1;
    border-radius: 5px;
    background-color: #999;
    position: relative;
    cursor: pointer;
    display: flex;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
}

.VideoController .seek-bar .seek {
    width: 50%;
    height: 100%;
    border-radius: 5px;
    background-color: #44F;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
}

.VideoController .ControlButton {
    width: 95%;
    display: flex;
    margin-bottom: 5px;
    flex: 1;
    align-items: center;
    justify-content: space-between;
}

.VideoController .ControlButton .icon {
    width: 25px;
    height: 25px;
    margin: 0px 8px;
    padding: 5px;
    border-radius: 50%;
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.VideoController .ControlButton .icon:hover {
    background-color: rgba(100, 100, 100, 0.5);
}

.VideoController .ControlButton .VolumeBox .icon:hover {
    background-color: rgba(100, 100, 100, 0.0);
}

.VideoController .ControlButton .PlayBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 10px;
    padding-right: 60px;
}

.VideoController .ControlButton .VolumeBox {
    display: flex;
    width: 30px;
    flex-direction: row;
    border-radius: 20px;
    padding: 0px 0px;
    align-items: center;
    justify-content: baseline;
    margin-right: 15px;
    transition: width 0.5s ease, opacity 0.3s ease;
}

.VolumeBox .seek-bar {
    max-height: 5px;
    width: 0;
    height: 5px;
    opacity: 0;
    flex: 1;
    border-radius: 5px;
    background-color: #999;
    position: relative;
    cursor: pointer;
    display: flex;
    transition: width 0.3s ease, opacity 0.3s ease;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
}

.VolumeBox .seek-bar.hidden {
    display: none;
    width: 0;
    opacity: 0;
}

.VolumeBox .seek-bar.visible {
    display: flex;
    width: 50px;
    margin-right: 20px;
    opacity: 1;
}

.VideoController .VolumeBox .seek {
    height: 100%;
    border-radius: 5px;
    background-color: #2E2;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
}

.VideoController .VolumeBox .thumb {
    position: absolute;
    top: -50%;
    width: 10px;
    height: 10px;
    background-color: #FFF;
    border-radius: 50%;
    pointer-events: none;
}

.VideoController .ControlButton .VolumeBox:hover {
    width: 120px;
    background-color: rgba(255, 255, 255, 0.1);
}

.VideoController .ControlButton p {
    margin-left: 5px;
    font-size: 13px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}