.menu-bar {
    background-color: #111;
    color: white;
    padding: 20px;
    text-align: center;
    flex-direction: column;
    transition: width 0.1s ease;
    gap: 15px;
    /* 크기 변경 시 애니메이션 */
}

.menu-bar .menu-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 15px;
    padding: 10px 10px;
    gap: 5px;
    transition: background-color 0.3s;
    cursor: pointer;
}

.menu-bar .compact-item {
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    border-radius: 15px;
    display: flex;
    gap: 5px;
    font-size: 12px;
    cursor: pointer;
}

.menu-bar .compact-item:hover, .menu-bar a:hover {
    background-color: #333;
}

.menu-bar .icon {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    margin-left: 5px;
    filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(0deg) brightness(100%) contrast(100%);
}

.menu-bar a {
    display: flex;
    color: #FFF;
    text-decoration: none;
    align-items: center;
    justify-content: baseline;
    border-radius: 15px;
}

