.VideoPlayer {
    position: relative;
    display: flex;
    max-width: 960px;
    max-height: 540px;
    min-width: 640px;
    min-height: 360px;
    border-radius: 15px;
    background-color: #333;
    overflow: hidden;
}

.VideoPlayer .Video {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    object-fit: contain;
    background-color: #333;
    border-radius: 15px;
}

