.VideoMenu .radiobox {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin: 0;
    padding: 0px;
    gap: 10px;
    background-color: #F00;
    border-color: 1px solid black;
}

.VideoMenu .radiobox .radio-row {
    background-color: #000;
    border: 1px solid white;
}

.VideoMenu .radiobox input {
    appearance: none;
    width: 10px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #ccc;
    outline: none;
    transition: border-color 0.3s;
    zoom: 1.0;
}

.VideoMenu .radiobox label {
    display: inline-block;
    align-items: center;
    text-decoration: none;
    margin: 0;
    padding: 10px;
}