.VideoMenu {
    position: absolute;
    display: flex;
    bottom: 70px;
    right: 5px;
    overflow: hidden;
    max-width: 100%;
    max-height: 100%;
    border-radius: 15px;
    padding: 30px;
    opacity: 0;
    background-color: rgba(50, 50, 50, 0.8);
    box-shadow: 1px 1px 5px #000;
    transform: translateX(100%);
    transition: transform 0.4s, opacity 0.4s;
    transform-origin: right;
}

.VideoMenu-Container {
    max-width: 100%;
    max-height: 100%;
}

.VideoMenu-Container, .VideoMenu-Container p {
    padding: 0px;
    margin: 0px;
}

.VideoMenu-Container .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.VideoMenu-Container select {
    width: 100px;
    justify-content: center;
    border-radius: 5px;
    padding: 2px 5px;
}