.login-page {
  background-color: #000;
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.login-container {
  display: flex;
  flex-direction: column;
  background-color: #222;
  color: #fff;
  padding: 30px;
  border-radius: 8px;
  text-shadow: 1px 1px 3px #000;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid black;
  width: 320px;
  height: fit-content;
  text-align: center;
  opacity: 1;
  transform: translateX(0%);
  transition: transform 0.4s, opacity 0.4s;
  transform-origin: center;
  z-index: 1;
}

.login-container h2 {
  margin-bottom: 20px;
}

.login-container form {
  display: flex;
  flex-direction: column;
}

.login-container label {
  margin-bottom: 5px;
  text-align: left;
}

.login-container input {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.login-container button {
  padding: 10px;
  font-size: 14px;
  text-shadow: 1px 1px 1px #000;
  background-color: #1bb622;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-container button:hover {
  background-color: #218838;
}

.login-container .box {
  margin: 5px;
  padding: 5px;
  flex: 1;
  cursor: pointer;
}

.login-container .box:hover {
  background-color: #218838;
  border-radius: 10px;
}


.login-container .error {
  margin-top: 10px;
  color: #aa0000;
  font-size: 13px;
}

.RowBox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2px 2px;
  gap: 3px;
}

.RowBox .naver-button {
  background-color: #1ec800;
  color: white;
  display: flex;
  flex: 1;
  align-items: center;
}

.RowBox .kakao-button {
  background-color: #edcc3f;
  color: #fff;
  text-shadow: 1px 1px 2px #000, 0px -1px 2px #000;
  flex: 1;
}

.RowBox .google-button {
  background-color: #3275e4;
  color: white;
  flex: 1;
}

.RowBox .naver-button:hover {
  background-color: #218838;
}

.RowBox .kakao-button:hover {
  background-color: #aa9900;
}

.RowBox .google-button:hover {
  background-color: #3568b9;
}

.RowBox .naver-button img, .kakao-button img, .google-button img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
  box-shadow: #333;
}

.Green {
  color: #1bb622;
  display: inline-block;
  cursor: pointer;
}