.App {
  background: #333;
  display: flex;
  height: 100vh;
  flex-direction: column;
}

.ActionBar {
  background: black;
  display: flex;
  color: #FFF;
  height: 60px;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
}

.ActionBar a {
  text-decoration: none;
  color: white;
}

.ActionBar .LogoBar {
  display: flex;
  width: 270px;
  min-width: 270px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.ActionBar .LogoBar .Setup {
  width: 20px;
  height: 30px;
  margin-right: 5px;
  border-radius: 5px;
  padding: 5px;
  color: white;
  cursor: pointer;
}

.ActionBar .LogoBar .Setup:hover {
  background-color: #1f2b5f;
}

.ActionBar .LogoBar h1 {
  text-decoration: none;
  padding: 5px;
  margin: 0px;
  font-size: 25px;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.ActionBar .LogoBar h1:hover {
  background-color: #1f2b5f;
}

.ActionBar .SearchBar {
  background-color: #FFF;
  border-radius: 25px;
  max-width: 500px;
  min-width: 200px;
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 15px;
}

.ActionBar .SearchBar input {
  border: none;
  outline: none;
  align-items: center;
  flex: 1;
}

.ActionBar .SearchBar img {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.ActionBar .InfoBar {
  display: flex;
  width: 230px;
  min-width: 180px;
  justify-content: end;
  align-items: center;
  padding: 0px 5px;
  gap: 20px;
}

.ActionBar .InfoBar img {
  align-items: center;
  justify-content: center;
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(0deg) brightness(100%) contrast(100%);
  fill: none;
  width: 25px;
  height: 25px;
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
}


.ActionBar .InfoBar .menu {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  padding-right: 10px;
}

.ActionBar .InfoBar .menu:hover {
  background-color: #1f2b5f;
}


.Content {
  background-color: #FFF;
  flex: 1;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

.StatusBar {
  background-color: #000;
  color: #aaa;
  font-size: 12px;
  height: 30px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.Green {
  color: #1bb622;
  display: inline-block;
}

.Yellow {
  color: #dbc923;
}