.PlayerPage {
    display: flex;
    height: 100vh;
    flex-direction: row;
    background-color: #000;
    color: #FFF;
    align-items: center;
    justify-content: space-between;
    overflow-y: auto;
}

.player {
    display: flex;
    flex: 1;
    background-color: #F00;
    align-items: center;
    justify-content: center;
    min-width: 640px;
    min-height: 360px;
    padding: 20px;
}

.playlist {
    display: flex;
    width: fit-content;
    align-items: center;
    background-color: #222;
}

@media (max-width:1080px) {
    .playlist {
        display: none;
    }
}