@font-face {
  font-family: 'Laundry';
  src: url('../public/fonts/laundry_regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Laundry';
  src: url('../public/fonts/laundry_bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

html, body {
  height: 100%;
}

body {
  font-family: Laundry;
  background-color: #fff;
  color: #333;
  display: flex;
  flex-direction: column;
}

/* WebKit 기반 브라우저용 스크롤바 스타일 */
::-webkit-scrollbar {
  width: 12px; /* 스크롤바의 너비 */
}

::-webkit-scrollbar-track {
  background: #f1f1f1; /* 트랙 배경색 */
  border-radius: 3px; /* 트랙의 둥근 모서리 */
}

::-webkit-scrollbar-thumb {
  background: #888; /* 핸들 배경색 */
  border-radius: 3px; /* 핸들의 둥근 모서리 */
}

::-webkit-scrollbar-thumb:hover {
  background: #555; /* 호버 시 핸들 배경색 */
}